import React, { useEffect, useState } from "react";
import Input from "../../Atoms/Input.atom";
import Button from "../../Atoms/Button.atom";
import { StyledAuthToken } from "./styled";

const AuthToken = () => {
	const [apiAdminKey, setapiAdminKey] = useState("");

	const addKey = () => {
		localStorage.setItem("apiAdminKey", apiAdminKey);
		console.log(localStorage.getItem("apiAdminKey"));
	};

	return (
		<StyledAuthToken>
			<Input getInput={setapiAdminKey} />
			<Button
				src={"enter key"}
				srcFunc={addKey}
			/>
		</StyledAuthToken>
	);
};

export default AuthToken;
